@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  padding-top: 3.5rem;
  font-family: sans-serif;
  background: linear-gradient(90deg, rgb(14, 226, 205), rgb(34, 68, 206));
}
@font-face {
	font-family: 'SEGA LOGO FONT';
	font-style: normal;
	font-weight: normal;
	src: local('SEGA LOGO FONT'), url(../../static/media/SEGA.12a3db8b.woff) format('woff');
}
::-webkit-input-placeholder {
	text-align: center;
}
::-ms-input-placeholder {
	text-align: center;
}
::placeholder {
	text-align: center;
}

.roboTitle {
	font-family: 'SEGA LOGO FONT';
	font-size: 4rem;
	font-weight: 200;
	color: #0afff7;
}

input {
	font-family: 'Roboto', sans-serif;
	font-size: 1.3rem;
	border-radius: 1rem;
	outline: 0;
	margin-top: 5rem;
	margin-bottom: 3rem;
	text-align: center;
}

.bg-light-green-custom {
	background: #65f1bd;
}
